
@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Montserrat");

/* Body */
body {
    font-family: "Montserrat",  sans-serif;
    font-size: 18px;
    margin: 0;
    padding: 20px;
}


/* Poch'Lib */
body #myBooks > h1 {
    padding-top: 1%;
    text-align: center;
    border: 3px #eeeded;
    height: 40px;
    line-height: 30px;
    font-weight: normal;
    font-family:"Montserrat",  sans-serif;
    background-color: #eff0f4;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
  }
  
/*nouveau livre*/
body #myBooks > h2 {
    text-align: center;
    font-family:"Montserrat",  sans-serif;
  }

/* Séparateur */
body #myBooks > hr {
    background-color:black; border:none; height:1px; margin-top: 2%; margin-bottom: -1%;
}

/*ma poch'liste*/
#content > h3 {
    text-align: center;
    font-family: "Source Code Pro", monospace;
    font-weight: bolder;
    margin-top: 1%;
    margin-bottom: 1%;
    }